.story-section {
	position: relative;
	@include min-lg {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.story {
		position: relative;
		width: 45%;
		padding: 5rem 0;
		@include max-lg {
			width: 60%;
			margin: 0 auto;
			padding: 2rem 0 5rem;
		}
		@include max-md {
			padding: 2rem 0 3rem;
			width: 100%;
		}
	}
	.story-content {
		position: relative;
		padding-right: 5rem;
		padding-left: 3rem;
		@include max-lg {
			padding: 0 1rem;
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		@include min-lg {
			text-align: left;
			.title-section {
				text-align: left;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 55%;
		aspect-ratio: 6/5;
		height: auto;
		@include max-lg {
			width: 100%;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
