.rsvp-section {
	position: relative;
	padding: 5rem 0;
	background-color: var(--color-primary);
	color: var(--color-light);
	@include max-md {
		padding: 3rem 0;
	}
	.city {
		position: absolute;
		top: 0;
		left: 50%;
		width: 105%;
		transform: translate(-50%, 10%);
		@include max-xl {
			width: 110%;
		}
		@include max-lg {
			width: 150%;
		}
		@include max-sm {
			width: 250%;
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-primary);
			opacity: 0.6;
			content: "";
			border-radius: var(--border-radius);
		}
		.rsvp-form {
			position: relative;
		}
	}
}
