.apology-section {
	position: relative;
	background-color: var(--color-primary);
	color: var(--color-light);
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.elements-wrapper-absolute {
		top: 20%;
		.background-element {
			img {
				object-position: 50% 0%;
			}
		}
	}
	.appology-wrap {
		position: relative;
	}
}
