.couple-section {
	position: relative;
	background-color: var(--color-primary);
	color: var(--color-light);
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 36px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0 10rem;
		@include max-md {
			padding: 3rem 0 10rem;
			.couple-container {
				padding: 0;
			}
		}
		.city {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 250%;
			transform: translate(-50%, 0);
		}
	}
	.couple-content {
		position: relative;
		padding: 2rem 1rem;
	}
	.couple {
		position: relative;
		text-align: center;
		border-radius: 300px 300px 20px 20px;
		overflow: hidden;
		padding: 3rem 3rem 0;
		&-body {
			position: relative;
			padding: 0.75rem 0.75rem 2rem;
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				border-radius: 300px 300px 0 0;
				opacity: 0.4;
				content: "";
			}
		}
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 3/4;
			margin: 0 auto;
			.image-element {
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					width: 100%;
					border-radius: 300px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
