.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.background-element {
		img {
			object-position: 50% 100%;
		}
	}
	.city {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 105%;
		transform: translate(-50%, 25%);
		@include max-lg {
			width: 150%;
		}
		@include max-sm {
			width: 200%;
			transform: translate(-50%, 15%);
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		z-index: 5;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 300px;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			color: var(--color-light);
			&-body {
				position: relative;
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			&::before {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: calc(100% + 15rem);
				background-color: var(--color-primary);
				opacity: 0.4;
				border-radius: var(--border-radius);
				content: "";
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
