.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		--color-button-trans: #fff;
		position: relative;
		margin-top: 2rem;
		rect {
			stroke: var(--color-button-trans);
		}
	}
	.elements-wrapper-absolute {
		transform: translateY(-32%);
		@include max-xl {
			transform: translateY(-15%);
		}
		@include max-lg {
			transform: translateY(-10%);
		}
	}
	.city {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 105%;
		transform: translate(-50%, 25%);
		@include max-lg {
			width: 150%;
			transform: translate(-50%, 10%);
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		padding: 6.5rem 0 0;
		display: flex;
		justify-content: center;
		@include max-lg {
			padding-top: 8rem;
		}
		@include max-sm {
			padding-top: 4.5rem;
		}
		.header {
			position: relative;
			width: 30%;
			height: 100%;
			color: var(--color-light);
			@include max-xl {
				width: 35%;
			}
			@include max-lg {
				width: 50%;
			}
			@include max-md {
				width: 40%;
			}
			@include max-sm {
				width: 95%;
			}
			&::before {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				border-radius: 300px 300px 0px 0px;
				background-color: var(--color-primary);
				opacity: 0.4;
				content: "";
			}
			.header-title {
				position: relative;
				text-align: center;
				padding: 8rem 0 3rem;
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
